.card {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 5rem;
  border-radius: 2rem;
  background-color: transparent;
  box-shadow: 0.4rem 0.4rem 0.8rem 0 rgba(61, 60, 60, 0.7),
    -0.4rem -0.4rem 0.8rem 0 rgba(0, 0, 0);
  padding: 5rem 5rem;
  transition: all 0.2s;
}

.card:hover {
  transform: scale(1.04);
}

.left_side,
.right_side {
  width: 50%;
}

.actions {
  width: 100%;
  display: flex;
}

.actions a {
  width: 100%;
  color: red;
  padding: 1rem 1.5rem;
  background-color: transparent;
  box-shadow: 0.2rem 0.2rem 0.4rem 0 rgba(61, 60, 60, 0.7),
    -0.2rem -0.2rem 0.2rem 0 rgba(0, 0, 0);
  font-size: 1.6rem;
  font-weight: 600;
  transition: all 0.3s;
  /* position: relative; */
}

.actions a::after {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 3rem;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 1;
  transition: all 0.3s;
}

.actions a {
  transition: all 0.3s;
}

.actions a:hover {
  transform: translateY(-0.3rem);
}

.actions a:hover::after {
  background-color: red;
  transform: scaleX(1.3) scaleY(1.3);
  opacity: 0;
}

a:visited,
a:link {
  text-decoration: none;
  position: relative;
}

.btn:first-of-type {
  margin-right: 1.5rem;
}

h3 {
  color: white;
}

.project_title {
  color: white !important;
}
